.plate {
    position:fixed;
    bottom:calc(-200% + 64px);
    width:100%;
    height:200%;
    display:flex; justify-content:center;

    z-index:100;
}
.plate-box {
    width:100%; height:100%; max-width:640px;
    background:rgb(var(--white));
    box-shadow:0 8px 16px rgba(0,0,0,0.2);
    border-radius:32px 32px 0 0;
    box-sizing:border-box;

    transform:translate3d(0, calc(0px - env(safe-area-inset-bottom)), 0);
    border:1px solid rgba(128,128,128,0.5);

    position: relative;
    overflow:hidden;
}
.plate-box.open {
    transform:translate3d(0, max(calc(-50% + 128px), -840px), 0);
}

.plate-box-wrap {
    display:flex;
    flex-direction:column;
    height:50%;
    position:relative;
    z-index:1;
}

.card-dragbar {
    position:absolute;
    width:64px;
    height:6px;
    top:0;
    left:50%;
    transform:translateX(-50%);

    box-sizing:content-box;
    padding:12px;
    z-index:10;
    user-select:none;
}
.card-dragbar > div {
    width:100%;
    height:100%;
    background:rgba(var(--black),0.2);
    border-radius:calc(100% / (64 / 6)) / 100%;
    transform:translate3d(0,0,0);
}
.card-title-box {
    display:flex;
    align-items:center;
    height:64px;
    position:relative;
    z-index:9;
    transform:translate3d(0,0,0);
}
.card-title {
    font-weight:500;
    padding:0 24px;
    position:relative;
}
.card-title .title-text {
    position:absolute;
    top:50%; left:50%; transform:translate(-50%, -50%);
}

.card-content-box {
    flex:1;
    overflow-y:auto;
    position:absolute;
    width:100%;
    height:100%;
    z-index:1;
    transform:translate3d(0,0,0);
}

.transition {
    transition:all 0.4s;
}
.plate .row {
    padding:32px 24px 0;
    min-height:120px;
    position:relative;

    counter-reset:num;
}
.plate .row::after {
    content:'';
    display:block;
    height:48px;
}
.plate .row p {
    margin-bottom:12px;
}
.plate .row p:last-of-type {
    margin-bottom:0;
}
.card-content-box > div:nth-of-type(1) {
    padding:0 24px;
}
.card-content-box > div:nth-of-type(2n) {
    background:rgb(var(--paper));
}
.card-content-box > div:nth-of-type(2n) .title {
    background:rgba(var(--paper), 0.75);
}

.title {
    font-size:1.5em;
    font-weight:bold;
    position:sticky;
    top:0px;
    background:rgba(var(--white),0.75);
    -webkit-backdrop-filter:blur(6px);
    backdrop-filter:blur(6px);
    height:64px;
    transform:translate3d(-24px, 0, 0);
    width:calc(100% + 48px);
    padding:0 24px;
    z-index:9;
    margin-bottom:12px;

    display:flex;
    align-items:center;
}
.title > .scroll-animation {
    transform-origin:left;
    // position:absolute;
    // top:50%;
    // transform:translateY(-50%);
}

.num-p {
    display:flex;
    justify-content:flex-start;
    counter-increment:num;
    white-space:nowrap;
    margin-bottom:12px;
}
.num-p > p {
    white-space:normal;
    flex:1;
}
.num-p::before{
    content:counter(num);
    margin-top:0.125em;
    margin-right:8px;
    width:20px; height:20px;
    font-size:0.875em;
    background:#1167ab;
    color:#fff;
    display:flex;
    justify-content:center;
    align-items:center;
    border-radius:50%;
}

.install {
    background:#1167ab;
    border-radius:9999px;
    padding:4px 12px;
    font-size:0.875em;
    color:#fff;
}

.binder {
    width:100%; height:1px;
    background:rgba(0,0,0,0.1);
}

.in-document-link {
    display:inline-flex;
    align-items:center;
    color:#1167ab;
}
.in-document-link > span:not(.material-icons){
    text-decoration:underline;
}
.in-document-link > .material-icons {
    margin-left:2px;
    font-size:100%;
    transform:translateY(1px);
}

.code-box {
    padding:8px 12px;
    background:#1a1a1a;
    color:#fff;
    border-radius:4px;
    white-space:pre-wrap;
    font-family: 'Nanum Gothic Coding', monospace;
    line-height:1.25em;
    font-size:0.875em;
    margin-bottom:48px;
}
code {
    font-family: 'Nanum Gothic Coding', monospace;
}